:root{--bg-color:#f0f0f0;background-color:var(--bg-color)}body,html{margin:0;padding:0;font-size:.93rem;font-family:sans-serif}#header{width:100%;height:56px;background-color:#4557af}#container{width:100%;height:auto;position:relative;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start}#navbar{width:260px;height:calc(100vh - 56px);background-color:transparent}#content{width:calc(100vw - 560px);height:calc(100vh - 56px);background-color:#fff;padding:32px 16px;-webkit-box-sizing:border-box;box-sizing:border-box;-webkit-box-shadow:0 5px 4px rgba(0,0,0,.23);box-shadow:0 5px 4px rgba(0,0,0,.23)}